<template>
  <div class="errorPage">
    <div class="center">
      <div class="number noselect">{{ getLabel }}</div>
      <Button text="Вернуться на главную" type="secondary" link="/" />
    </div>
  </div>
</template>

<script>
import Button from "@/features/Button.vue";

export default {
  components: { Button },
  props: ["label", "title"],
  mounted() {
    document.title = this.getLabel + " | dvegasa";
  },
  computed: {
    getLabel() {
      if (this.label) return this.label;
      return this.$route.meta.errorLabel ?? "";
    },
  },
};
</script>

