<template>
  <a
    v-if="external"
    class="button"
    :class="typeClass"
    @mouseover="isHovered = true"
    @mouseleave="isHovered = false"
    :href="link"
    :target="newTab ? '_blank' : ''"
  >
    <Icon class="icon" :icon="icon" v-if="icon" />
    <div class="text" v-if="showText" :class="textClass">
      {{ text }}
    </div>
  </a>

  <router-link
    v-else
    class="button"
    :class="typeClass"
    @mouseover="isHovered = true"
    @mouseleave="isHovered = false"
    :to="link"
    :target="newTab ? '_blank' : ''"
  >
    <Icon class="icon" :icon="icon" v-if="icon" />
    <div class="text" v-if="showText" :class="textClass">
      {{ text }}
    </div>
  </router-link>
</template>

<script>
import { Icon } from "@iconify/vue";

export default {
  components: { Icon },
  data() {
    return {
      isHovered: false,
    };
  },
  props: ["text", "icon", "type", "link", "newTab", "external"],
  computed: {
    typeClass() {
      return "type-" + this.type;
    },
    textClass() {
      let c = [];
      if (this.text && !this.icon) {
        c.push("textOnly");
      }
      if (this.text && this.icon) {
        c.push("textWithIcon");
      }
      c.push("text-" + this.type);
      return c;
    },
    expandable() {
      return false;
    },
    showText() {
      if (!this.expandable && this.text) return true;
      if (this.expandable && this.isHovered) return true;
      return false;
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/styles";

.button {
  height: 38px;
  font-size: 14px;
  border-radius: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.33));

  &:hover {
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.1));
  }

  .icon {
    font-size: 20px;
    width: 38px;
    color: white;
  }
}

.textOnly {
  padding: 10px 18px;
}

.textWithIcon {
  padding-right: 10px;
}

.text-primary {
  color: $colorBlack;
}

.text-secondary {
  color: $colorWhite;
}

.type-primary {
  background-color: $colorAccent;

  &:hover {
    background-color: $colorAccent-light;
  }
}

.type-secondary {
  background-color: $colorBlack;

  &:hover {
    background-color: $colorBlack-light;
  }
}
</style>
