<template>
  <router-view id="router-view" />
</template>

<script>
export default {
  mounted() {
    document.title = "dvegasa";
  },
  watch: {
    $route: {
      immediate: true,
      handler(to) {
        let t = "";
        if (to.meta.title === "~errorLabel") t = to.meta.errorLabel;
        else if (to.meta.title) t = to.meta.title;
        document.title = t + " | dvegasa";
      },
    },
  },
};
</script>

<style lang="scss">
@import "@/styles";

.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
}

#router-view {
  width: 100%;
  height: 100%;
}
</style>
